<template>
  <div>
    <el-row>
      <el-button type="primary" size="medium" @click="dialogVisible = true; form.names = ''; isEdit = false">新增分类</el-button>
    </el-row>
    <el-table :data="Product">
      <el-table-column prop="names" label="分类名称" style="text-align: left">
      </el-table-column>
      <el-table-column label="操作" width="120">
        <template slot-scope="scope">
          <el-button-group>
            <el-tooltip class="item" effect="dark" content="编辑" placement="top">
              <el-button plain icon="el-icon-edit" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      v-if="pages > 1"
      layout="prev, pager, next"
      :total="pages">
    </el-pagination>
    <el-dialog
      title="添加商品分类"
      :visible.sync="dialogVisible"
      width="30%"
      >
      <el-form :model="form" label-position="right" label-width="100">
        <el-form-item label="分类名称" label-width="100">
          <el-input v-model="form.names" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleCreate">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<style lang="scss" scoped>
  .el-row {
    text-align: left;
    margin-bottom: 20px;
  }
  .el-table {
    width: 50%;
  }
</style>

<script>
import {mallClassList, mallClassCreate, mallClassRemove, mallClassModify} from '../utils/fetchModel'
import ElRow from 'element-ui/packages/row/src/row'
export default {
  components: {ElRow},
  data () {
    return {
      Product: [],
      pages: 0,
      dialogVisible: false,
      form: {
        names: ''
      }
    }
  },
  created () {
    this.$loading({
      spinner: 'el-icon-loading',
      customClass: 'loading',
      background: 'rgba(0, 0, 0, 0.6)'
    })
    this.fetchData();
  },
  methods: {
    fetchData () {
      mallClassList().then((res) => {
        setTimeout(() => {
          this.Product = res.data.lists;
          this.pages = res.data['paging.pagetotal'];
          this.$loading().close()
        }, 1000)
      })
    },
    handleDelete (index, row) {
      console.log(index, row)
      const {classid, names} = row;
      this.$alert(`您确定删除商品分类：${names} 吗？`
        ).then((confirm) => {
          mallClassRemove({classid}).then((res) => {
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.fetchData();
          });
        }).catch(() => {});
    },
    handleEdit (index, row) {
      const {classid, names} = row;
      this.form.names = names;
      this.dialogVisible = true;
      this.isEdit = classid;
    },
    handleCreate () {
      const {names} = this.form;
      if(!names || names === '') {
        this.$message({
          message: '请输入分类名称',
          type: 'warning'
        });
        return;
      }
      if (!!this.isEdit) {
        mallClassModify({classid: this.isEdit}, {names}).then((res) => {
          this.dialogVisible = false;
          this.fetchData();
        });
        return;
      }
      mallClassCreate({names}).then((res) => {
        this.dialogVisible = false;
        this.fetchData();
      });
    }
  }
}
</script>



