<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="商品名称" prop="topic" :rules="[{ required: true, message: '请输入商品名称'}]">
        <el-input v-model="form.topic" type="topic"></el-input>
      </el-form-item>
      <el-form-item label="商品简介">
        <el-input v-model="form.summary"></el-input>
      </el-form-item>
      <el-form-item label="商品描述">
        <el-input type="textarea" v-model="form.remark"></el-input>
      </el-form-item>
      <el-form-item label="商品分类" prop="classid" :rules="[{ required: true, message: '请选择商品分类'}]">
        <el-select v-model="form.classid" type="classid" placeholder="请选择商品分类">
          <el-option :key="item.classid" v-for="item in classList" :label="item.names" :value="item.classid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="price" :rules="[
      { required: true, message: '商品价格不能为空'},
      { type: 'number', message: '商品价格必须为数字值'}
    ]" label="商品价格">
        <el-input v-model.number="form.price" type="price"></el-input>
      </el-form-item>
      <el-form-item prop="total_data" :rules="[
      { required: true, message: '商品库存不能为空'},
      { type: 'number', message: '商品库存必须为数字值'}
    ]" label="商品库存">
        <el-input v-model.number="form.total_data" type="price"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
  .el-row {
    text-align: left;
    margin-bottom: 20px;
  }
  .el-form {
    width: 740px;
    padding: 20px;
    text-align: left;
    border-raduis: 5px;
    background: #ffffff;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, .12);
  }
</style>

<script>
import {mallClassList, memberCreate, memberInfo, memberModify} from '../utils/fetchModel'
import ElRow from 'element-ui/packages/row/src/row'
export default {
  components: {ElRow},
  created () {
    if (this.$route.query.no) {
     this.fetchMallData();
     return;
    }
    this.fetchClassData();
  },
  data() {
    return {
      classList: [],
      form: {
        topic: '',
        price: 0,
        classid: '',
        summary: '',
        remark: '',
        total_data: 0
      }
    }
  },
  methods: {
    onSubmit(formName) {
      const {no} = this.$route.query;
      this.$refs[formName].validate((valid) => {
          if (valid) {
            if (no) {
              memberModify({no}, this.form).then((res) => {
              this.$alert('修改成功').then(() => {
                this.$router.push('/Product');
              })
            });
              return;
            }
            memberCreate(this.form).then((res) => {
              this.$alert('添加成功').then(() => {
                this.form = {
                  topic: '',
                  price: 0,
                  classid: '',
                  summary: '',
                  remark: ''
                };
                this.$refs[formName].resetFields();
              })
            });
          } else {
            return false;
          }
        });
    },
    fetchClassData() {
        mallClassList().then((res) => {
          this.classList = res.data.lists;
        });
    },
    fetchMallData() {
      const {no} = this.$route.query;
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
      mallClassList().then((res) => {
        this.classList = res.data.lists;
        memberInfo({no}).then((res) => {
          this.form = res.data;
          this.$loading().close();
        });
      });
    }
  }
}
</script>



