<template>
  <div>
    <el-row>
      <el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search">
      </el-input>
    </el-row>
    <el-table :data="Product">
      <el-table-column prop="id" label="商品ID" width="140">
      </el-table-column>
      <el-table-column prop="topic" label="商品名称" width="120">
      </el-table-column>
      <el-table-column prop="summary" label="商品描述">
      </el-table-column>
      <el-table-column prop="price" label="价格">
      </el-table-column>
      <el-table-column prop="total_data" label="总量">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button-group>
            <el-tooltip class="item" effect="dark" content="下单" placement="top">
              <el-button type="primary" icon="iconpet-right" size="mini" @click="handleOrder(scope.$index, scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="详情" placement="top">
              <el-button type="info" icon="iconpet-createtask" @click="handleInfo(scope.$index, scope.row)" size="mini"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="编辑" placement="top">
              <el-button plain icon="iconpet-editor" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" icon="iconpet-trash" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="商品详情" :visible.sync="dialogVisible">
      <p>名称：{{info.topic}}</p>
      <p>简介：{{info.summary}}</p>
      <p>备注：{{info.remark}}</p>
      <p>价格：{{info.remark}}</p>
    </el-dialog>
    <el-pagination
      background
      v-if="pages > 1"
      layout="prev, pager, next"
      :total="pages">
    </el-pagination>
  </div>
</template>

<style lang="scss" scoped>
  .el-row {
    text-align: left;
    margin-bottom: 20px;
  }
</style>

<script>
import {mallProductList, mallProductRemove, mallProductInfo} from '../utils/fetchModel'
import ElRow from 'element-ui/packages/row/src/row'
export default {
  components: {ElRow},
  data () {
    return {
      Product: [],
      pages: 1,
      dialogVisible: false,
      info: {}
    }
  },
  created () {
    this.fetchData();
  },
  methods: {
    fetchData () {
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      })
      mallProductList().then((res) => {
        this.Product = res.data.lists;
        this.pages = res.data['paging.pagetotal'];
        this.$loading().close();
      })
    },
    handleOrder (index, row) {
      console.log(index, row)
    },
    handleEdit (index, row) {
      this.$router.push({
          path: '/ProductAdd', 
          name: 'ProductAdd',
          query: {no: row.no}
      });
    },
    handleInfo (index, row) {
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
      mallProductInfo({no: row.no}).then((res) => {
        this.info = res.data;
        this.$loading().close();
        this.dialogVisible = true;
      });
    },
    handleDelete (index, row) {
      this.$confirm('您确定要删除' + row.topic + '?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          mallProductRemove({no: row.no}).then((res) => {
            this.fetchData();
          });
        }).catch(() => {});
    }
  }
}
</script>



