<template>
  <div>
    <el-row>
      <el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search">
      </el-input>
    </el-row>
    <el-table :data="member">
      <el-table-column prop="id" label="会员ID" width="140">
      </el-table-column>
      <el-table-column prop="names" label="名字" width="120">
      </el-table-column>
      <el-table-column prop="mobile" label="手机号" width="120">
      </el-table-column>
      <el-table-column prop="amout" label="余额">
      </el-table-column>
      <el-table-column prop="points" label="积分">
      </el-table-column>
      <el-table-column prop="summary" label="备注">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button-group>
            <el-tooltip class="item" effect="dark" content="详情" placement="top">
              <el-button type="info" icon="iconpet-createtask" @click="handleInfo(scope.$index, scope.row)" size="mini"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="编辑" placement="top">
              <el-button plain icon="iconpet-editor" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <el-button type="danger" icon="iconpet-trash" size="mini" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="会员详情" :visible.sync="dialogVisible">
      <div class="inners">
        <p>名字：{{info.topic}}</p>
        <p>手机号：{{info.mobile}}</p>
        <p>余额：{{info.amount}}</p>
        <p>积分：{{info.points}}</p>
        <p>备注：{{info.summary}}</p>
      </div>
    </el-dialog>
    <el-pagination
      background
      v-if="pages > 1"
      layout="prev, pager, next"
      :total="pages">
    </el-pagination>
  </div>
</template>

<style lang="scss" scoped>
  .el-row {
    text-align: left;
    margin-bottom: 20px;
  }
  .el-dialog .inners {
    text-align: left;
    margin: 0 auto;
    width: 180px;
  }
</style>

<script>
import {memberList, memberRemove, memberInfo} from '../utils/fetchModel'
import ElRow from 'element-ui/packages/row/src/row'
export default {
  components: {ElRow},
  data () {
    return {
      member: [],
      pages: 1,
      dialogVisible: false,
      info: {}
    }
  },
  created () {
    this.fetchData();
  },
  methods: {
    fetchData () {
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      })
      memberList().then((res) => {
        this.member = res.data.lists;
        this.pages = res.data['paging.pagetotal'];
        this.$loading().close();
      })
    },
    handleOrder (index, row) {
      console.log(index, row)
    },
    handleEdit (index, row) {
      this.$router.push({
          path: 'MmemberAdd', 
          name: 'MemberAdd',
          query: {no: row.no}
      });
    },
    handleInfo (index, row) {
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
      memberInfo({no: row.no}).then((res) => {
        this.info = res.data;
        this.$loading().close();
        this.dialogVisible = true;
      });
    },
    handleDelete (index, row) {
      this.$confirm('您确定要删除' + row.topic + '?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          memberRemove({no: row.no}).then((res) => {
            this.fetchData();
          });
        }).catch(() => {});
    }
  }
}
</script>



