<template>
  <div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="姓名" prop="names" :rules="[{ required: true, message: '请输入会员姓名'}]">
        <el-input v-model="form.names" type="names"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="mobile" :rules="[{ required: true, message: '请输入会员手机号'}]">
        <el-input v-model="form.mobile" type="mobile"></el-input>
      </el-form-item>
      <el-form-item prop="amount" :rules="[
        { required: true, message: '余额不能为空'},
        { type: 'number', message: '余额必须为数字值'}
      ]" label="余额">
        <el-input v-model.number="form.amount" type="amount"></el-input>
      </el-form-item>
      <el-form-item prop="points" :rules="[
        { required: true, message: '积分不能为空'},
        { type: 'number', message: '积分必须为数字值'}
      ]" label="积分">
        <el-input v-model.number="form.points" type="points"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="form.summary"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
  .el-row {
    text-align: left;
    margin-bottom: 20px;
  }
  .el-form {
    width: 740px;
    padding: 20px;
    text-align: left;
    border-raduis: 5px;
    background: #ffffff;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, .12);
  }
</style>

<script>
import {memberCreate, memberInfo, memberModify} from '../utils/fetchModel'
import ElRow from 'element-ui/packages/row/src/row'
export default {
  components: {ElRow},
  created () {
    if (this.$route.query.no) {
     this.fetchMemberData();
     return;
    }
  },
  data() {
    return {
      classList: [],
      form: {
        mobile: 0,
        names: '',
        points: 0,
        amount: '',
        summary: ''
      }
    }
  },
  methods: {
    onSubmit(formName) {
      const {no} = this.$route.query;
      this.$refs[formName].validate((valid) => {
          if (valid) {
            if (no) {
              memberModify({no}, this.form).then((res) => {
              this.$alert('修改成功').then(() => {
                this.$router.push('/Product');
              })
            });
              return;
            }
            memberCreate(this.form).then((res) => {
              this.$alert('添加成功').then(() => {
                this.form = {
                  names: '',
                  points: 0,
                  amount: '',
                  summary: '',
                  mobile: 0
                };
                this.$refs[formName].resetFields();
              })
            });
          } else {
            return false;
          }
        });
    },
    fetchMemberData() {
      const {no} = this.$route.query;
      this.$loading({
        spinner: 'el-icon-loading',
        customClass: 'loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
      memberInfo({no}).then((res) => {
        this.form = res.data;
        this.$loading().close();
      });
    }
  }
}
</script>



