function jsonToUrlParams (json) {
  let url = ''
  for (let i in json) {
    url += `&${i}=${json[i]}`
  }
  return url.substr(1, url.length - 1)
}

function fetch (api, method = 'post') {
  const ua = localStorage.getItem('ua');
  if (api.param.hasSid) {
    api.url += `?${jsonToUrlParams({ sid: JSON.parse(ua).sid })}`;
    delete api.param.hasSid;
    if (api.param.action) {
      api.url += `&${jsonToUrlParams({ action: api.param.action })}`;
      delete api.param.action;
    }
  } else if(api.param.action) {
    api.url += `?${jsonToUrlParams({ action: api.param.action })}`;
    delete api.param.action;
  }
  api.param = api.param || {}
  if (!(api.isMock || MOCK) && method.toLowerCase() === 'post') {
    const post = api.post || api.param
    let url = baseUrl + api.url;
    if (api.post) {
      if (url.indexOf('?') > -1) {
        url += `&${jsonToUrlParams(api.param)}`;
      } else {
        url += `?${jsonToUrlParams(api.param)}`;
      }
    }
    return Vue.http.post(
      url,
      post,
      {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    ).then((res) => {
      if (res.body.status !== 'succeed') {
        Message({
          message: res.body.message,
          type: 'error'
        });
        return null
      }
      if (api.showMsg) {
        Message({
          message: res.body.message,
          type: 'success'
        });
      }
      return res.body
    }).catch((err) => {
      console.log(err)
    })
  } else if (!(api.isMock || MOCK) && method.toLowerCase() === 'get') {
    const getUrl = `${baseUrl}${api.url}&${jsonToUrlParams(api.param)}`
    return Vue.http.get(
      getUrl,
      {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    ).then((res) => {
      if (res.body.status !== 'succeed') {
        Message({
          message: res.body.message,
          type: 'error'
        });
        return null
      }
      if (api.showMsg) {
        Message({
          message: res.body.message,
          type: 'success'
        });
      }
      return res.body
    }).catch((err) => {
      console.log(err)
    })
  } else {
    return Vue.http.get(
      `../../static${api.url}`,
      {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    ).then((res) => {
      return res
    }).catch((err) => {
      console.log(err)
    })
  }
}



