<template>
  <div class="login-box">
    <div class="inners">
      <el-form :model="form" status-icon :rules="rules" ref="form" label-width="55px" class="demo-ruleForm">
        <el-form-item label="用户名" prop="account">
          <el-input type="text" v-model="form.account" auto-complete="off" placeholder="请输入手机号"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password" auto-complete="off" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-row>
          <el-button type="primary" @click="submitForm('form')">登录</el-button>
          <el-button @click="resetForm('form')">清除</el-button>
        </el-row>
      </el-form>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../sass/var";
  .login-box {
    background: url("../assets/login.jpg") center center;
    background-size: cover;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    .inners {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      width: 360px;
      padding: 40px;
      border-radius: 8px;
      background: #ffffff;
    }
  }
</style>
<script>
import md5 from 'js-md5'
import {login} from '../utils/fetchModel'
export default {
  data () {
    const validateName = (rule, value, callback) => {
      if (!value || value === '') {
        return callback(new Error('用户名不能为空'))
      }
      callback()
    }
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      }
      callback()
    }
    return {
      form: {
        account: '',
        password: ''
      },
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        account: [
          { validator: validateName, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const param = {...this.form, password: md5(this.form.password).substr(8, 16)}
          login(param).then((res) => {
            if (res) {
              localStorage.setItem('ua', JSON.stringify(res.data))
              this.$cookieStore.setCookie('ua', JSON.stringify(res.data));
              this.$router.push('/')
            }
          })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>



